#asideRight{
    padding: 5px;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    border-radius: 0rem 0rem 0rem 1.5rem;
    top: 12rem;
    right: 0%;
    width: 25%;
    /* background-color: darkred; */
    background-color: 	rgb(0, 0, 15);
    max-width: 25rem;
}

#divCerrarAsideRight{
    display: flex;
    align-self: end;
    outline: solid 0.2rem rgb(114, 114, 114);
    border-radius: 50%;
}
#divCerrarAsideRight:hover{
    cursor: pointer;
    outline: solid 0.2rem rgb(0, 0, 0);
}
#imgCerrarAsideRight{
    height: 2rem;
    width: 2rem;
    border: 50%;
}
#imgCerrarAsideRight:hover{
    cursor: pointer;
}

#divOpciones{
    padding: 5px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    border-radius: 0rem 0rem 0rem 1rem;
    /* background-color: darkolivegreen; */
}
.botonOpciones{
    width: 100%;
    padding-top: 0.5rem ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.anclaOpciones{
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    /* background-color: rgb(7, 57, 57); */
    padding: 5px;
}
.anclaOpciones:hover{
    cursor: pointer;
}
.iconoOpciones{
    height: 5rem;
}
.tituloIconoOpciones{
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
}
@media (min-width: 700px) {
    aside{
        width: 20%;
    }
    .botonOpciones{
        /* flex-direction: row; */
    }
    .anclaOpciones{
        flex-direction: row;

    }

}
@media (min-width: 1000px) {
    aside{
        width: 17%;
    }
}
@media (min-width: 1300px) {
    aside{
        width: 15%;
    }
}