footer{
    display: flex;
    align-items: center;
    background-color: #01010e;
    gap: 1rem;
    padding: 1.5rem 1rem 1rem 1rem;
}


#logoFooter{
    height: 2.8rem;  
}

#contenedorInputFooter{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

#textInputFooter{
    text-align: center;
}
#inputFooter{
    height: 2.5rem;
    font-size: 1.3rem;
    padding-left: 0.5rem;
    width: 100%;
    color: black;
}
#parrafoValidacionInputFooter{
    display: flex;
    padding: 0.4rem 0rem 0rem 0.4rem;
    visibility: hidden;
}
#divBotonSuscribirFooter{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
}
#botonSuscribirFooter{
    height: 2.5rem;
    width: 60%;
    font-size: 1.3rem;
    background-color: rgb(4, 130, 4);

}


#contenedorTelefonoFooter{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

#divSocialFooter{
    display: flex;
    gap: 1rem;
}
.iconoSocialFooter{
    height: 2.7rem;
}

#divTelefonoFooter{
    display: flex;
    flex-direction: column;
    gap: 0.1rem;
}

#numeroTelefono{
    text-decoration: underline;
}
#numeroTelefono:hover{
    cursor: pointer;
}