@font-face {
    font-family: tituloBebidas;
    src: url("../Media/tituloBebidas.ttf");
}

#divContenedorBebidas{
    display: none;
    flex-direction: column;
    position: absolute;
    top: 20rem;
    justify-content: space-between;
    width: 28rem;
    border: rgb(5, 45, 65) solid 8px;
    filter: drop-shadow(0rem 0rem .7rem rgb(92, 92, 92));
    border-radius: 0rem 1rem 1rem 0rem;
    backdrop-filter: blur(3px);
}
@media (min-width: 550px) {
    #divContenedorBebidas{
        width: 35rem;
    }
}

#fondoBlurTitulo{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 0;
    left: 0;
    background-color: rgba(106, 106, 106, 0.2);
}
#divTituloBebidas{
    display: flex;
    padding: 1rem;
    justify-content: center;
    flex-direction: column;
    background-image: url("../Media/fondoBebidas3.jpg");
    border-radius: 0rem 1rem 0rem 0rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: 1;
    padding-bottom: 4rem;
}


#divIconoCerrarBebidas{
    display: flex;
    align-self: end;
    outline: solid 0.2rem rgb(114, 114, 114);
    border-radius: 50%;
}
#divIconoCerrarBebidas:hover{
    cursor: pointer;
    outline: solid 0.2rem rgb(0, 0, 0);
}


#imgIconoCerrarBebidas{
    width: 2rem;
    height: 2rem;
    /* border: solid 0.2rem rgb(180, 180, 180); */
    border-radius: 50%;
}



#divTituloOpcionesBebidas{
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    background-color: rgba(0, 0, 0, 0.75);
    padding: 1rem 2rem 1rem 2rem ;
    border: solid 0.3rem rgb(160, 160, 160);
    border-radius: 0.6rem;
}

#tituloOpcionesBebidas{
    font-size: 3rem;
    font-family: tituloBebidas;
    letter-spacing: 0.3rem;
    color: aqua;
}


#divContenidoBebidas{
    z-index: 1;
    display: grid;
    flex-direction: column;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    border-top: solid 0.2rem rgb(160, 160, 160);
    border-left: solid 0.4rem rgb(160, 160, 160);
    border-right: solid 0.4rem rgb(160, 160, 160);
    border-bottom: solid 0.4rem rgb(160, 160, 160);
    
}

.divTipoBebidas{
    display: flex;
    position: relative;
    flex-direction: column;
}

.divBlurTipos{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    /* background-color: rgba(84, 84, 84, 0.15);
    backdrop-filter: blur(2.2px); */
}

.divContenedorTipo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    gap: 1rem;
    padding: 1rem;
}



.divImgTipoBebidas{
    display: flex;
    justify-content: center;
    align-items: center;
}

.imgTipoBebidas{
    width: 7rem;
}
.imgTipoBebidas:hover{
    cursor: pointer;
}



.divTxtTipoBebidas{
    display: flex;
    flex-direction: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
    border: solid 2px rgb(80, 80, 80);
    border-radius: 4px;
    padding: 0.5rem 2rem 0.5rem 2rem;
    /* width: 100%; */
}

.txtTipoBebidas{
    font-size: 1.5rem;
    font-weight: 900;
}

.divBotonesBebidas{
    display: flex;
    gap: 1rem;
    
}
.divBoton{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(10, 151, 118);
    border: solid 3px rgb(0, 0, 0);
    border-radius: 50%;
    padding: 5px;
}
.divBoton:hover{
    background-color: rgb(240,95,36, 1);
    cursor: pointer;
    border: solid 3px rgb(255,255,0);
}

.imgBotonBebidas{
    height: 1.6rem;
    width: 1.6rem;
}


