#nav{
    display: flex;
    width: 100%;
    justify-content: space-between;
}


#contenedorBannerNav{
    display: flex;
    padding-left: 1rem;
}
#bannerNav{
    margin-top: -1rem;
    height: 13rem;
    width: 8rem;
}
#logoBannerNav{
    position: absolute;
    top: 1.2rem;
    left: 1.4rem;
    width: 7rem;
    height: 7rem;
}

#contenedorSitesUser{
    width: 75%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: 	rgb(0, 0, 15);
    border-radius: 0rem 0rem 0rem 1.5rem;
}
#sites{
    display: flex;
    gap: 1.5rem;
    width: 75%;
    justify-content: center;
    /* gap: 10%; */
}
.sitesBoton{
    user-select: none;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    
}
.divImgSites{
    display: flex;
    background-color: rgb(240,95,36);
    padding: 0.8rem;
    border-radius: 50%;
}

.imgSites{
    width: 3rem;
}
.tituloSites{
    text-align: center;
    font-weight: bold;
}


.sitesBoton:hover{
    cursor: pointer;
    color: rgb(255,255,0);
}
.sitesBoton:hover .divImgSites{
    background-color: rgb(255,255,0);
}
.sitesBoton:hover .tituloSites{
    color: rgb(255,255,0);
}

#botonPedidos{
    position: relative;
}

#divContadorPedidos{
    display: flex;
    width: 2rem;
    height: 2rem;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -1.3rem;
    left: -.7rem;
    background-color: rgb(0, 0, 0);
    outline: solid 0.2rem rgb(240,95,36);
    padding: 0.5rem;
    border-radius: 50%;
}
#textoContadorPedidos{
    font-size: 1.3rem;
}
#divContadorProductos{
    display: flex;
    width: 2rem;
    height: 2rem;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -1.3rem;
    right: -.7rem;
    background-color: rgb(0, 0, 0);
    outline: solid 0.2rem rgb(240,95,36);
    padding: 0.5rem;
    border-radius: 50%;
}
#textoContadorProductos{
    font-size: 1.3rem;
}

#user{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25%;
}

#divUser{
    display: flex;
    gap: 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#divAvatarUser{
    background-color: darkgray;
    height: 5.7rem ;
    aspect-ratio: 1 / 1;
    user-select: none;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    outline: solid 0.2rem white;
}
#divAvatarUser:hover{
    cursor: pointer;
    outline: solid 0.2rem black;
}
#avatarUser{
    width: 3rem;
}

@media (min-width: 550px) {
    #contenedorSitesUser{
        padding-inline: 2rem;
        width: 65%;
    }
}


@media (min-width: 700px) {
    #contenedorSitesUser{
        width: 55%;
        padding-inline: 3rem;
    }
    #sites{
        gap:15%;
    }
}

@media (min-width: 780px) {
    .tituloSites{
        font-size: 1.3rem;
        
    }
}
@media (min-width: 1100px) {
    .tituloSites{
        font-size: 1.8rem;
    }
}

@media (min-width: 1000px) {
    #contenedorSitesUser{
        width: 50%;
    }
}

@media (min-width: 1100px) {
    #divContadorPedidos, #divContadorProductos{
        top: -0.8rem;
    }
    #divContadorPedidos{
        left: 0rem;
    }
    #divContadorProductos{
        right: 0rem;
    }
}

@media (min-width: 1300px) {
    #contenedorSitesUser{
        width: 45%;
    }
}