#landingHome{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: end;
    width: 100%;
    min-height: 93vh;
    background-image: url("../Media/fondoHome.jpg") ;
    background-repeat: no-repeat;
    background-position: center; 
    background-size: cover;
    background-size: 100% 100%; 
    margin-top: -14rem;
}

#splashHome{
    display: flex;
    position: relative;
}


#pizzaLanding{
    position: absolute;
    right: 0%;
    z-index: 1;
    width: 29rem;
}

#splashLanding{
    height: 30rem;
}

@media (min-width: 500px) {
    #landingHome{
        height: 100vh;
    }
}