#landingPedidos{
    display: flex;
    height: 93vh;
    
    justify-content: center;

    background-image: url("../Media/fondoPedidosBOX.jpg") ;
    background-repeat: no-repeat;
    background-position: 75% 0%;
    background-size: cover;

    margin-top: -14rem;
    padding-top: 19rem;
    padding-bottom: 3rem;
}

#divContenedorPedidos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    background-image: url("../Media/doodleBGBig.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    backdrop-filter: blur(3px);
    background-color: rgba(16, 16, 16, 0.35);
    border-radius: 1.5rem;

    width: 90%;
    /* height: 100%; */

    border: solid 2px rgba(196, 196, 196, 1);

}



.divPedidos{
    display: flex;
    flex-direction: column;
    /* border-radius: 1rem; */
    width: 90%;
    height: 95%;
    gap: 1rem;
    /* padding: 1rem; */
}

.divContenedorNombrePedido{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(0, 0, 0, 0.7);
    padding: 0.5rem;
    border-radius: 5px;
}

.divNombrePedido{
    display: flex;
}
.tituloNombrePedido{
    font-size: 3rem;
}

.botonesVerificarPedido{
    display: flex;
    gap: 0.5rem;
    align-items: end;
    align-self: end;

}
.divBotonVerificarPedido{
    display: flex;
    justify-content: center;
    align-items: center;
}
.imgBotonVerificarPedido{
    width: 4.5rem;
}
.divBotonVerificarPedido:hover{
    cursor: pointer;
}


.divContenedorProductos{
    display: flex;
    flex-direction: column;
    /* overflow: auto; */
}
.divContenedorProductos::-webkit-scrollbar {
    display: none;
}
.divContenedorProductos{
    -ms-overflow-style: none;
    scrollbar-width: none;
}


.botonesCategorias{
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: start;
    background-color: rgba(142, 134, 134, 0.335);
    border-radius: 1rem 1rem 0 0rem;
    backdrop-filter: drop-shadow(3px 3px 3px rgba(255, 255, 255, 1));
}
.divBotonCategoriaPedido{
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 2rem;
}
.divBotonCategoriaPedido:hover{
    cursor: pointer;
}
.imgBotonCategorias{
    width: 4.5rem;
}






.divBotonCategoriaPizzas{
    display: flex;
    border-radius: 1rem 1rem 0 0rem;
    background-color: rgba(245, 245, 245, 0.384);
}
.divBotonCategoriaBebidas{
    display: flex;
    border-radius: 1rem 1rem 0 0rem;
}
.divBotonCategoriaCombos{
    display: flex;
    border-radius: 1rem 1rem 0 0rem;
}

.divPanelesCategorias{
    display: flex;
    flex-direction: column;
    height: 39rem;
    gap: 2rem;
}
.divCategoria{
    display: flex;
    flex-direction: column;
    height: 46vh;
    padding: 1rem;
    background-color: rgb(5, 5, 5, 0.8);
    border-radius: 0rem 1rem 1rem 1rem;
}
.divCategoriaPizzas{
    display: flex;
}
.divCategoriaBebidas{
    display: none;
}
.divCategoriaCombos{
    display: none;
}


.divTituloCategoria{
    display: flex;
    padding-left: 2rem;
    padding-top: 1rem;
}
.tituloCategoria{
    font-size: 3.5rem;
}

.divProductos{
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 3rem;
    overflow: auto;
}
.divProductos::-webkit-scrollbar {
    display: none;
}
.divProductos{
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.divProductoDetalles{
    background-color: rgba(255, 255, 255, 0.1);
    display: flex;
    flex-direction: column;
}
.divBotonesProducto{
    display: flex;
    align-self: end;
    gap: 0.8rem;
    background-color: rgb(235, 235, 235);
    padding: 0.5rem;
    border-radius: 1rem 1rem 0rem 0rem ;
}
.divBotonProducto{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3rem;
    outline: solid 2px rgb(29, 8, 1);
    border-radius: 50%;
}
.divBotonProducto:hover{
    cursor: pointer;
    outline: solid 2px rgb(177, 67, 3);
}
.divBotonProductoDelete:hover{
    background-color: red;
    outline: solid 2px rgb(29, 8, 1);
}
.imgBotonProducto{
    width: 2rem;
    border-radius: 50%;
}


.divInformacionProducto{
    background-color: rgb(23, 23, 23);
    color: black;
    padding: 1.5rem;
    display: flex;
    justify-content: space-between;
    padding-inline: 0.5rem;
}
.divInformacionProducto p{
    /* color: black; */
}
.contenedorInformacionProducto{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem;
}
.tituloDetallesProducto{
    font-size: 2rem;
    font-weight: bolder;
    text-align: center;
}
.informacionDetallesProducto{
    text-align: center;
    font-size: 1.5rem;
    font-weight: bolder;
}

@media (min-width: 395px) {
    #landingPedidos{
        height: 100vh;
    }
}

@media (min-width: 400px) {
    .divPanelesCategorias{
        height: 36rem;
    }
}
