#contenedorDisplaysMenu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;

    backdrop-filter: blur(3px);
    background-color:  rgba(0, 0, 0, 0.3);
    border-radius: 2rem; 
    
    width: 95%;
    /* flex-wrap: wrap; */

    border: solid 2px rgba(196, 196, 196, 0.6);
}

#divDisplaysMenu{
    display: flex;
    flex-direction: column;
    background-color: rgba(31, 31, 31, 0.323);
    padding: 2rem;
    backdrop-filter: blur(3px);
    border-radius: 1rem;
    align-items: center;
    /* width: 90%; */
    height: 95%;
    gap: 2rem;
    overflow: scroll;
}
#divDisplaysMenu::-webkit-scrollbar {
    display: none;

}
#divDisplaysMenu {
    -ms-overflow-style: none;
    scrollbar-width: none;
}



.displayPizza{
    background-image: url("../Media/blackboarddisplaypizza.jpg");
    background-repeat: no-repeat;
    background-position: bottom; 
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    padding-block: 2rem;
    border-radius: 0.5rem;
    border: solid 2px rgb(163, 119, 97);
    width: 100%;
    max-width: 40rem;
    height: 100%;
    max-height: 49rem;

}
.divTituloDisplay{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15%;
    /* background-color: darkblue; */
    width: 100%;
}
.divImgDisplay{
    display: flex;
    height: 40%;
    padding: 0.5rem;
    /* background-color: darkcyan; */
    width: 100%;
    /* width: 100%; */
    justify-content: center;
    align-items: center;
}
.divDetailsDisplay{
    /* background-color: rgb(135, 42, 111); */
    width: 100%;
    height: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
}
.divButtonDisplay{
    /* background-color: darkgoldenrod; */
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tituloMenu{
    font-family: chalkFontTitle;
    font-size: 3rem;
    letter-spacing: 0.3rem;
    opacity: 0.8;
}
.imgDisplayMenu{
    height: 100%;
}
.descripcionMenu, .precioMenu{
    font-size: 1.8rem;
    font-family: chalkFontDetails;
    color: #bbabab;
    text-align: center;
}
.simboloMoneda{
    font-family: chalkFontDetails;
    font-size: 1.6rem;
    color: rgb(0, 150, 0);
}




.botonAñadir{
    background-color: rgb(13, 92, 13);
    padding: 0.5rem 0.7rem 0.5rem 0.7rem;
    width: 8rem;
    font-size: 1.5rem;
}
.botonAñadir:hover{
    cursor: pointer;
    background-color: rgb(7, 130, 7);
}


@media (min-width: 400px) {
    #landingPizzasMenu{
        height: 100vh;
    }
}

@media (min-width: 1000px) {
    #divDisplaysMenu{
        display: grid;
        justify-items: center;
        grid-template-columns: 1fr 1fr;
    }
}

@media (min-width: 1450px) {
    #divDisplaysMenu{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
}
